import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
export default {
	name: 'agency',
	props: {
		item: {
			type: String,
			default: ''
		},
		roleId: {
			type: String,
			default: ''
		},
		userApply:{
			type: Object,
			default: new Object()
		}
	},
	data() {
		return {
			shippers: ['我是代办', '我要卖货'],
			spListValue: '158.55',
			spGoodsByAgency: [{
					img: '../../static/imgs/icon-1003.png',
					text: '我的货品'
				},
				{
					img: '../../static/imgs/icon-1002.png',
					text: '本地代办'
				}
			],
			spGoodsByShipper: [{
					img: '../../static/imgs/icon-1031.png',
					text: '发布新品'
				},
				{
					img: '../../static/imgs/icon-1032.png',
					text: '全部产品'
				},
				{
					img: '../../static/imgs/icon-1033.png',
					text: '货主'
				}
			],
			spOrders: [{
				img: '../../static/imgs/icon-1004.png',
				text: '待确认'
			}, {
				img: '../../static/imgs/icon-1005.png',
				text: '待买家支付'
			}, {
				img: '../../static/imgs/icon-1006.png',
