	},
	mounted() {
		console.log('------')
		console.log(this.roleId)

	},
	methods: {
		// 去审核页面
		goRealname(){
			uni.navigateTo({
				url: '/pages/middle/identity/realname/agency'
			})
		},
		//  去订单页面
		goOrder(){
			uni.navigateTo({
				url: '/pages/order/goodsList/goodsList'
