		goRelease() {
			uni.navigateTo({
				url: '/pages/middle/release/release'
			})
		},
		// 产品页面
		goGoodsCheck(i){
			let index = i
			// 2001 货主  2002代办
			if(this.roleId == 2001){
				if(index === 1){
					uni.navigateTo({
						url:'/pages/middle/release/product/localproduct/localproduct'
					})
				}else if(index === 2){
					uni.navigateTo({
						url:'/pages/middle/release/product/localshipper/localshipper'
					})
				}
			}
		}
	}
}
